import { UserModel } from "../../model/user"
const userModel = new UserModel();
export default {
    name: "order",
    data () {
        return {
            active:0,
            array: ['价格不合理', '保障信息不全', '流程不对', '暂时没有这方面的需求'],
            page: 1,
            size: 4,
            flag: true,
            list: []
        }
    },
    components:{

    },
    methods:{
        scrolltolower() {
            if(this.flag == true) {
                this.applyProjectList();
            }
        },
        applyProjectList() {
            let params = {
                page: this.page,
                size: this.size
            }
            userModel.applyProjectList(params).then((res) => {
                console.log(res);
                if(res.code == 200) {
                    this.list = this.list.concat(res.data);
                    if(res.data.length == 0) {
                        this.flag = false;
                    }else {
                        this.flag = true;
                        this.page = this.page + 1;
                    }
                }
            });
        }
    },
    onLoad(option){
       this.applyProjectList();
    },
    onShow(){
        
    }
}
